<template>
  <v-container fluid pa-0>
    <v-col
      class="pa-0"
      align="center" 
      justify="center"
      wrap
    >
      <v-carousel :height="imgHeight" tile cycle hide-delimiter-background show-arrows-on-hover>
        <v-carousel-item 
          v-for="(item,i) in mainItems" 
          :key="i"
          :src="require(`@/assets/portfolio/${item.src}`)" 
          transition="slide"
        >
        <v-col cols="12" align="start" class="main_item" :class="item.color">
          <p class="main_title">{{item.title}}</p>
          <p class="main_sub">{{item.sub}}</p>
          <!-- <p class="main_date">{{item.date}}</p> -->
          <img :src="require(`@/assets/portfolio/${item.icon}`)" class="icon_img">
        </v-col>
        </v-carousel-item>
      </v-carousel>
      <v-col class="col-lg-10">
        <v-row align="center" justify="start">
          <v-col cols="6" sm="6" md="4" class="main_card_item" v-for="(item1,i) in mainItems" :key="i">
            <v-card
              class="mx-auto"
              max-width="100%"
              outlined
              v-on:click="link(i)"
            >
              <v-img
                class="grey--text align-end main_item_img"
                :src="require(`@/assets/portfolio/${item1.src}`)"
              >
                <!-- <v-card-title>{{item1.title}}</v-card-title> -->
              </v-img>
              <v-col align="start">
                <v-card-text class="main_item_title">{{item1.title}}</v-card-text>
                <v-card-subtitle class="main_item_text">{{item1.text}}</v-card-subtitle>
                <v-col align="end" class="pa-0">
                  <v-card-text class="main_item_text text--primary ">{{item1.date}}</v-card-text>
                </v-col>
              </v-col>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-col>
  </v-container>
</template>

<script>
import $ from "jquery";
export default {
  data: () => ({
    imgHeight: 700,
    smallWidth: false
  }),
  computed:{
    mainItems(){
      return this.$store.getters.getMain;
    }
  },
  created(){
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  mounted(){
    this.$store.commit('changeTitle','main project');
    if(this.smallWidth == true){
      this.$eventBus.$emit('drawerHome', false);
    }
  },
  methods:{
    handleResize(){
      var width = window.innerWidth;
      // width = 1264 이하 일때 동작하지 않음
      if( width > 1264 ){
        this.smallWidth = true;
        this.imgHeight = 700;
      }if( width < 768 ){
        this.imgHeight = 600;
      }if( width < 600 ){
        this.smallWidth = false;
        this.imgHeight = 500;
      }
    },
    link(value){
      this.$store.dispatch('changeMain',value);
      this.$router.push('/mainContents').catch(err => err);
    }
  },
  updated(){
  },
  watch:{
  },
  beforeDestroy(){
    if(this.smallWidth == true){
      this.$eventBus.$emit('drawerHome', true);
    }
    window.removeEventListener('resize', this.handleResize)
  }
}
</script>

<style>
.light-text{
  color: #fff !important;
}
.dark-text{
  color: #192E49 !important;
}
.main_item{
  padding: 5rem;
  text-align: left;
}
.main_title{
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: uppercase;
}
.main_sub{
  font-size: 1.2rem;
  font-weight: 500;
  text-transform: uppercase;
}
.main_item_title{
  font-size: 1.3rem;
	font-weight: 600;
  text-align: left;
	color: #192E49;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* 라인수 */
	-webkit-box-orient: vertical;
	word-wrap:break-word;
  padding-bottom: 0;
	width:100%;
}
.main_item_text{
  font-size: 1rem;
	font-weight: 500;
  text-align: left;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2; /* 라인수 */
	-webkit-box-orient: vertical;
	word-wrap:break-word;
	width:100%;
}
.main_date{
  font-size: 1rem;
}
.icon_img{
  margin-top: 1.5rem;
  width: 100px !important; 
}
.main_item_img{
  height: 300px;
}
.main_card_item{
  padding: 3rem;
}
@media screen and (max-width: 1600px) {
  .main_card_item{
    padding: 1.5rem;
  }
}
@media screen and (max-width: 1200px) {
  .main_card_item{
    padding: 1rem;
  }
}
@media screen and (max-width: 768px) {
  .main_item_title{
    font-size: 1rem;
    padding: 0.6rem;
    width:100%;
  }
  .main_item_text{
    font-size: 0.85rem;
    padding: 1rem;
  }
  .main_item{
    padding: 3rem;
  }
  .main_title{
    font-size: 2rem;
  }
  .main_sub{
    font-size: 0.85rem;
  }
  .icon_img{
    width: 80px !important; 
  }
  .main_card_item{
    padding: 0.5rem;
  }
  .main_item_img{
    height: 200px;
  }
  .dark-text{
    background: linear-gradient(rgba( 255, 255, 255, 0.6 ),rgba( 255, 255, 255, 0.4 ),rgba( 255, 255, 255, 0 ));
  }
}
@media screen and (max-width: 600px) {
  .main_item_title{
    font-size: 0.85rem;
    padding: 0.2rem;
    width:100%;
  }
  .main_item_text{
    font-size: 0.6rem;
    padding: 0.4rem;
  }
}
</style>
